import "./App.css";
import { Switch, HashRouter as Router, Route, Link } from "react-router-dom";
import PokemonDetails from './components/PokemonDetails'
import Home from './components/Home'
import React, { useEffect, useState, useMemo } from "react";

function App() {
  const [pokemon, setPokemon] = useState([]);
  const [filteredPokemon, setFilteredPokemon] = useState([]);
  const [text, setText] = useState([]);
  useEffect(() => {
    fetch("https://pokeapi.co/api/v2/pokemon?offset=0")
      .then((res) => res.json())
      .then((data) => {
        const results = data.results.map((pokemon, idx) => {
          return { ...pokemon, idx: idx + 1 };
        });
        setPokemon({ ...data, results });
      });
  }, []);

  useMemo(() => {
    if (!text) {
      setFilteredPokemon([]);
      return;
    }
    setFilteredPokemon(() =>
      pokemon.results?.filter((pokemon) => pokemon.name.includes(text))
    );
  }, [text]);

  return (
    <Router>
      <div className="p-14 bg-gray-100">
        <div className="flex flex-col items-center">
          <Link to="/">
            <header className="text-4xl text-gray-700">Home Page</header>
          </Link>
        </div>
        <div className="w-full flex justify-center">
          <input
            onChange={($event) => setText($event.target.value)}
            placeholder="Search Pokemon..."
            type="text"
            className="mt-10 p-2"
          />
        </div>
        <Switch>
          <Route path="/about/:slug">
            <PokemonDetails>hi</PokemonDetails>
          </Route>
          <Route path="/">
            <Home pokemon={filteredPokemon} />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
